/*--------
__layout
-----------------------------------------------*/
#contactform .input_outer {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: center;
  box-sizing: border-box;
  text-align: left;
  margin: 1em 0;
}

@media screen and (max-width: 960px) {
  #contactform .input_outer {
    flex-wrap: wrap;
  }
}

#contactform .input_outer.singlenote {
  display: block;
  padding-left: 30%;
}

@media screen and (max-width: 960px) {
  #contactform .input_outer.singlenote {
    padding-left: 0;
    text-align: center;
  }
}

#contactform .input_outer.submit {
  margin-top: 1.5em;
  text-align: center;
  display: block;
  position: relative;
}

#contactform .input_head {
  width: 30%;
  text-align: right;
  padding-right: 1.5em;
  font-size: .85em;
  white-space: nowrap;
}

@media screen and (max-width: 960px) {
  #contactform .input_head {
    width: 100%;
    text-align: left;
  }
}

#contactform .input_head.required:after {
  content: '※';
  font-size: .75em;
  vertical-align: top;
  padding-left: .25em;
}

#contactform .input_wrap {
  width: 70%;
}

@media screen and (max-width: 960px) {
  #contactform .input_wrap {
    width: 100%;
  }
}

#contactform .input_wrap .double {
  margin-top: 1em;
}

#contactform .flex_col {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: center;
}

#contactform .flex_col span {
  flex: 0 1 auto;
}

@media screen and (max-width: 480px) {
  #contactform .flex_col {
    flex-wrap: wrap;
    overflow-x: scroll;
  }
}

#contactform .flex_col .flex_txt {
  padding-left: .5em;
  padding-right: .5em;
  font-size: .85em;
  display: block;
}

/*--------
__submit
-----------------------------------------------*/
#contactform input[type="submit"] {
  border-radius: 0;
  -webkit-box-sizing: content-box;
  -webkit-appearance: button;
  appearance: button;
  border: none;
  outline: none;
  font-size: 16px;
  display: inline-block;
  box-sizing: border-box;
  text-decoration: none;
  cursor: pointer;
  border: none;
  opacity: 1;
  box-sizing: border-box;
}

#contactform input[type="submit"]::-webkit-search-decoration {
  display: none;
}

#contactform input[type="submit"]::focus {
  outline-offset: -2px;
}

#contactform input[type="submit"]:disabled {
  opacity: 0.4;
  background: #ccc;
}

/*--------
__#accept
-----------------------------------------------*/
#contactform input[type='checkbox'] {
  filter: alpha(opacity=0);
  -moz-opacity: 0;
  opacity: 0;
  position: absolute;
}

#contactform .chk_target {
  display: block;
  cursor: pointer;
}

#contactform .chk_target:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  height: 28px;
  width: 28px;
  line-height: 28px;
  font-size: 28px;
  font-family: 'Font Awesome 5 Free';
  content: '\f058';
  text-rendering: auto;
  font-weight: bold;
  color: #ccc;
  transform: translateY(-50%);
  transition: all .15s linear;
}

#contactform .chk_target.c_on:before {
  color: #008650;
}

#contactform #accept .note {
  display: block;
  padding-top: .5em;
  padding-bottom: .5em;
  padding-left: 40px;
  cursor: pointer;
  font-size: 13px;
  line-height: 1.4;
}

@media screen and (max-width: 660px) {
  #contactform #accept .note {
    font-size: 16px;
  }
}

/*--------
__select
-----------------------------------------------*/
#contactform select {
  font-size: 16px;
  -moz-appearance: none;
  -webkit-appearance: none;
  -o-appearance: none;
  -ms-appearance: none;
  appearance: none;
  background: #fff;
  border: 2px solid #ddd;
  box-shadow: none;
  border-radius: 5px;
  resize: none;
  padding-left: 1em;
  padding-right: 2em;
  text-indent: 0.01px;
  text-overflow: '';
}

@media screen and (max-width: 660px) {
  #contactform select {
    padding-right: 1em;
  }
}

#contactform select::-ms-expand {
  display: none;
}

#contactform .select_wrap .wpcf7-form-control-wrap {
  position: relative;
}

#contactform .select_wrap .wpcf7-form-control-wrap::before {
  pointer-events: none;
  position: absolute;
  display: block;
  width: 1em;
  height: auto;
  right: .75em;
  top: 50%;
  font-family: 'Font Awesome 5 Free';
  content: '\f078';
  text-rendering: auto;
  color: #333;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  text-align: center;
  transform: translateY(-50%);
}

@media screen and (max-width: 660px) {
  #contactform .select_wrap .wpcf7-form-control-wrap::before {
    display: none;
  }
}

#contactform .select_wrap .wpcf7-form-control-wrap.flex_col .wpcf7-not-valid-tip {
  display: none;
}

@media screen and (max-width: 660px) {
  #contactform .select_wrap.nowrap select {
    font-size: 12px !important;
    padding-left: .75em;
    padding-right: .75em;
  }
  #contactform .select_wrap.nowrap .flex_txt {
    font-size: 10px;
  }
}

/*--------
__radio
-----------------------------------------------*/
#contactform .radio_wrapper {
  font-size: 16px;
}

#contactform .radio_wrapper .wpcf7-form-control-wrap label {
  cursor: pointer;
}

#contactform .radio_wrapper .wpcf7-form-control-wrap label input[type="radio"] {
  display: none;
}

#contactform .radio_wrapper .wpcf7-form-control-wrap label input[type="radio"]:checked + span {
  color: #333;
}

#contactform .radio_wrapper .wpcf7-form-control-wrap label input[type="radio"]:checked + span::after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 6px;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: #333;
  transform: translateY(-50%);
}

#contactform .radio_wrapper .wpcf7-form-control-wrap label span {
  padding-left: 30px;
  position: relative;
  margin-right: 20px;
  line-height: 1;
}

#contactform .radio_wrapper .wpcf7-form-control-wrap label span::before {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  width: 24px;
  height: 24px;
  border: 2px solid #ddd;
  border-radius: 50%;
