/*-----------------------------------------------
 __mainVisual
-----------------------------------------------*/
.l_mainVisual {
  height: 80vh;
  max-width: 1920px;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
  min-height: 800px;
  max-height: 960px;
  z-index: 1;
}

.l_mainVisual.home {
  background-image: url(../images/top_cover.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}

.l_mainVisual.home:before {
  content: '';
  position: absolute;
  top: -8%;
  left: 90%;
  background-image: url(../images/gra_bar__brn.svg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: left center;
  height: 160px;
  width: 800px;
  transform-origin: left top;
  animation: mv_bar__brn 1s ease .8s 1 normal forwards;
  transform: rotate(-45deg) translateX(70%);
}

.l_mainVisual.home:after {
  content: '';
  position: absolute;
  bottom: -20%;
  right: 42%;
  background-image: url(../images/gra_bar__org.svg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: left center;
  height: 160px;
  width: 800px;
  animation: mv_bar__org 1s ease 1.4s 1 normal forwards;
}

.l_mainVisual.about {
  background: #DEF0E3;
  overflow: inherit;
  height: 70vh;
  min-height: 700px;
  max-height: 700px;
}

.l_mainVisual.about:after {
  content: '';
  position: absolute;
  left: 48%;
  bottom: -5%;
  background-image: url(../images/pAbout_mv.png);
  background-size: 100%;
  background-repeat: no-repeat;
  background-position: center bottom;
  width: 90%;
  padding-top: 60%;
  transform: translate(-50%);
  z-index: 2;
}

.l_mainVisual__copy {
  position: absolute;
  top: 40%;
  right: 20%;
  transform: translateY(-50%);
}

.l_mainVisual__copy.about {
  right: auto;
  left: 50%;
  transform: translate(-50%, -50%);
}

.l_mainVisual__copy.about .read {
  color: #008650;
  text-align: center;
  display: block;
  padding-bottom: 1em;
}

.l_mainVisual__copy img {
  max-width: 100%;
  height: auto;
  display: block;
}

@keyframes mv_bar__brn {
  0% {
    transform: rotate(-45deg) translateX(70%);
  }
  100% {
    transform: rotate(-45deg) translateX(-50%);
  }
}

@keyframes mv_bar__org {
  0% {
    transform: rotate(135deg) translateX(40%);
  }
  100% {
    transform: rotate(135deg) translateX(-25%);
  }
}

