  background: #008650;
}

.md_glnavi__triger:hover span:nth-child(1) {
  animation: glnavi__triger1 1.5s ease-in-out 0s;
}

.md_glnavi__triger:hover span:nth-child(2) {
  animation: glnavi__triger1 1.5s ease-in-out .15s;
}

.md_glnavi__triger:hover span:nth-child(3) {
  animation: glnavi__triger1 1.5s ease-in-out .3s;
}

.md_glnavi__triger span {
  transition: all .5s linear;
  display: block;
  height: 2px;
  border-radius: 4px;
  background: #008650;
  position: absolute;
  width: 20px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.md_glnavi__triger span:nth-child(1) {
  top: calc(50% - 8px);
}

.md_glnavi__triger span:nth-child(3) {
  top: calc(50% + 8px);
}

@keyframes glnavi__triger1 {
  0% {
    left: -5px;
    opacity: 0;
    width: 0;
  }
  20% {
    left: 50%;
    opacity: 1;
    width: 20px;
  }
  100% {
    opacity: 1;
    width: 20px;
  }
}

